

<script>

  import {MDBRow, MDBCol, MDBContainer} from "mdbsvelte";
  import Line from "svelte-chartjs/src/Line.svelte"
  import * as d3 from 'd3';
  // Use d3 to load data.csv and then makeData
  // Data looks like this:
  // Item1, Item2
  // 1, 2
  // 3, 4
  d3.csv("./data/data.csv").then(makeData);

  // Create a dataline for each column in the data.csv file
  var dataLines = {};
  var searchBoxText = ""

  function makeData(data) {
    // Remove the spinner
    var columns = data.columns
    // The columns are the item names, each row contains the price for each time period
    for (var i = 0; i < columns.length; i++) {
      var item = columns[i]
      var values = data.map(function(d) { return d[item]; });
      // Remove all values of 0 from values
      values = values.filter(function(d) { return d != 0; });
      // Create dataline with labels 0, 1, 2, 3, etc. and values from the data
      var labels = d3.range(values.length).map(function(d) { return d; });
      var randomColor = getRandomColor();
      var background = "rgba(" + randomColor[0] + ", " + randomColor[1] + ", " + randomColor[2] + ", 0.25)"
      var border = "rgba(" + randomColor[0] + ", " + randomColor[1] + ", " + randomColor[2] + ", 1)"
      var dataLine = {
        labels: labels,
        datasets: [
          {
            label: item,
            backgroundColor: background,
            borderColor: border,
            borderWidth: 2,
            hoverBackgroundColor: background,
            hoverBorderColor: border,
            pointHitRadius: 10,
            lineTension: 0.175,
            data: values,
          }
        ]
      }
      dataLines[item] = dataLine;
    }

    // Get a random rgb color as an array of 3 numbers between 0 and 255
    function getRandomColor() {
      return [Math.floor(Math.random() * 256), Math.floor(Math.random() * 256), Math.floor(Math.random() * 256)];
    }
    

    var searchBox = document.getElementById("search-box");
    searchBox.onkeyup = function() {
      var text = searchBox.value;
      // Find the closest match to the text in the search box
      var closest = findClosest(text, columns);
      // Update the search box text to the closest match
      searchBoxText = closest;  
    }

    // Find the closest match to the text in the search box
    function findClosest(text, columns) {
      var closest = columns[0];
      var closestDistance = 100;
      for (var i = 0; i < columns.length; i++) {
        var distance = levenshtein(text, columns[i]);
        if (distance < closestDistance) {
          closest = columns[i];
          closestDistance = distance;
        }
      }
      return closest;
    }

    // Find the Levenshtein distance between two strings
    function levenshtein(a, b) {
      if (a.length == 0) return "";
      var d = []; //2d matrix
      var i, j, cost;
      if (a.length == 0) return b.length;
      if (b.length == 0) return a.length;
      for (i = 0; i <= b.length; i++) {
        d[i] = [i];
      }
      for (j = 1; j <= a.length; j++) {
        d[0][j] = j;
      }
      for (i = 1; i <= b.length; i++) {
        for (j = 1; j <= a.length; j++) {
          if (b.charAt(i - 1) == a.charAt(j - 1)) {
            cost = 0;
          } else {
            cost = 1;
          }
          d[i][j] = Math.min(d[i - 1][j] + 1, Math.min(d[i][j - 1] + 1, d[i - 1][j - 1] + cost));
        }
      }
      return d[b.length][a.length];
    }
  }


  
</script>
<MDBContainer fluid>
  <MDBRow>
    <MDBCol md=4>
      <input type="text" class="form-control" placeholder="Search..." id="search-box">
    </MDBCol>
  </MDBRow>
</MDBContainer>

<!-- Add the chart with a margin on all sides -->
<MDBRow>
  <MDBCol md="8" style="margin-left: auto; margin-right: auto; width: 80%">
    <Line data={dataLines[searchBoxText]} options={{ responsive: true }}></Line>
  </MDBCol>
</MDBRow>
